@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

body, #root {
  font-family: 'Open Sans';
  width: 100vw;
  height: 100vh;
  margin: 0;
  display: flex;
}

.calculator {
  margin: auto;
  background-color: #95afc0;
  height: 100%;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
}

button {
  height: 100%;
  font-weight: bolder;
  width: 100px;
  max-width: 25vw;
  /* width: 80px;
  line-height: 70px;
  height: 70px; */
  padding: 10px;
  background-color: #535c68;
  color: white;
  border-radius: 5px;
  box-shadow: 1px 2px 2px 1px rgba(0, 0, 0, .8);
  vertical-align: middle;
  text-align: center;
  font-size: 20px;
}

.calculator-pad {
  column-gap: 10px;
  row-gap: 5px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  background: none;
  margin: 5px auto;
}


.display, .output {
  background: #535c68;
  color: white;
  font-size: 8rem;
  margin: 0;
  text-align: center;
  justify-content: center;
  display: flex;
  overflow: hidden;
  max-width: 100vw;
}

.display > p, .output > p {
  border: 10px solid #c7ecee;
}

#equals {
  grid-row-start: 2;
  grid-row-end: 6;
  height: auto;
}

.display > p {
  width: auto;
  margin: 0 2px;
  padding: 0 5px;
  border-radius: 10px;
}